import React, { useState } from 'react';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './JsonUploadForm.css';

function JsonUploadForm({ onJsonLoad }) {
  const [fileInputKey, setFileInputKey] = useState(0); //    input file

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = (event) => {
      const jsonText = event.target.result;
      try {
        JSON.parse(jsonText);
        onJsonLoad(jsonText);
      } catch (error) {
        toast.error(":   . ,     JSON.");
      } finally {
        //    input file
        setFileInputKey(prevKey => prevKey + 1);
      }
    };

    reader.readAsText(file);
  };

  return (
    <div>
      <input key={fileInputKey} type="file" id="jsonFileInput" onChange={handleFileChange} style={{ display: 'none' }} />
      <button id="loadJsonButton" onClick={() => document.getElementById("jsonFileInput").click()}> JSON-</button>
      <ToastContainer position="top-center"/>
    </div>
  );
}

export default JsonUploadForm;
